  mounted() {
    this.initMap();
  },
  methods: {
    initMap(){
    this.map = new Map({
      target: this.$refs.mapEl,
      interactions: DefaultInteractions({
        doubleClickZoom: false, // 禁止双击放大地图
      }),
      view: new View({
        center: [117.828122,30.955014],
        zoom: 14,
        projection: "EPSG:4326",
        minZoom: 0,
        maxZoom: 18,
      }),
    });
    const { tileLayer, tileMark } = this.tianditu("vec_w","cva_w");
    this.map.addLayer(tileLayer);
    this.map.addLayer(tileMark);
    this.initPopup();
    },
    tianditu(a,b){
      const source = new XYZ({
        tileUrlFunction: this.tiandituUrl(a),
        //tileUrlFunction: this.tiandituUrl("vec_w"),
        //tileUrlFunction: this.tiandituUrl("img_w"),
      });
      const tileLayer = new TileLayer({
        source: source,
      });
      //标注图层
      let sourceMark = new XYZ({
        tileUrlFunction: this.tiandituUrl(b),
        //tileUrlFunction: this.tiandituUrl("cva_w"),
        //tileUrlFunction: this.tiandituUrl("cia_w"),
      });
      let tileMark = new TileLayer({
        source: sourceMark,
      });
      tileLayer.set("id",a);
      tileMark.set("id",b);
      return {
        tileLayer,
        tileMark,
      };
    },
    tiandituUrl(t) {
      return (tileCoord) => {
        const urlChannelArr = this.urlChannel.split(",");
        const urlChannelLen = urlChannelArr.length;
        const row = tileCoord[1];
        const curChannelIndex = row % urlChannelLen;
        const curChannel = urlChannelArr[curChannelIndex];
        return `http://t${curChannel}.tianditu.com/DataServer?T=${t}&tk=${key}&x=${tileCoord[1]}&y=${tileCoord[2]}&l=${tileCoord[0]}`;
      };
    },
    addVectorLayer(features,id,properties,minZoom,maxZoom){
      const geoFeatures = new GeoJSON().readFeatures(features);
      for (let i = 0; i < geoFeatures.length; i++) {
        if(geoFeatures[i].values_.用地性质.indexOf('工业') > 0){
          geoFeatures[i].setStyle(defaultIndustryStyle);
          geoFeatures[i]['defaultStyle'] = defaultIndustryStyle;
        }else{
          geoFeatures[i].setStyle(defaultStyle);
          geoFeatures[i]['defaultStyle'] = defaultStyle;
        }
        // geoFeatures[i].values_.用地性质.indexOf('工业') > 0 ? 
        // geoFeatures[i].setStyle(defaultIndustryStyle) : geoFeatures[i].setStyle(defaultStyle);
        
      }
      const layer = new Vector({
        properties,
        source: new sourceVector({
          features: geoFeatures,
          wrapX: false,
        }),
        //style: defaultStyle,
        zIndex: 9999999,
      });
      minZoom && layer.setMinZoom(minZoom);
      maxZoom && layer.setMinZoom(maxZoom);
      id && layer.set('id',id);
      this.map.addLayer(layer);
      return layer;
    },
    removeLayerById(id){
      let layers = this.map.getLayers();
      for (let i = 0; i < layers.getLength(); i++) {
        let layer = layers.item(i);
        if(layer!=undefined && layer.get("id") == id){
          this.map.removeLayer(layer);
          break;
        }
      }
    },
    switchBaseLayer (name) {
      var layers = this.map.getLayers();
      for (var i = 0; i < layers.getLength(); i++) {
        var layer = layers.item(i);
        if (layer.get("id") == "vector" || layer.get("id") == name) {
          layer.setVisible(true);
        } else {
          layer.setVisible(false);
        }
      }
    },
    initPopup(){
      this.popup = new Overlay({
        element: this.$refs.popup,
        autoPan: true,
        positioning: 'top-center',
        stopEvent: true,
        autoPanAnimation: {
          duration: 250
        }
      });
      this.map.addOverlay(this.popup);
      this.map.on('click',(e) => {
        const feature = this.map.forEachFeatureAtPixel(e.pixel, (feature, layer) => {
          return feature;
        });
       if(!feature) { 
        if(this.beforeFeature){
         this.beforeFeature.setStyle(this.beforeFeature['defaultStyle'])
        }
        this.popup.setPosition(undefined); return; 
      };
       const obj = {};
       for (const key in feature.getProperties()) {
        if(key !== 'geometry') obj[key] = feature.getProperties()[key];
       }
       this.current = feature;
       if(this.beforeFeature){
        this.beforeFeature.setStyle(this.beforeFeature['defaultStyle'])
       }
       this.current.setStyle(highlightStyle)
       this.beforeFeature = this.current
       this.curFeature = obj;
       this.year = obj['供应时间'].substring(0,4);
       this.popup.setPosition(e.coordinate);
      })
    },
    zoom(){
      if(!this.current) return;
      this.zoomToExtent(this.current.getGeometry().getExtent())
    },
    zoomToExtent(extent) {
      this.map.getView().fit(extent, { duration: 1000 });
   },
   goThree(v){
    console.log(v)
    let routeUrl = this.$router.resolve({
      path: '/ThreePhotos',
    });
    window.open(routeUrl.href,'_blank')
   }
  },
  watch: {
    mapData:{
      handler(newVal){
        this.vectorData = newVal
      }
    },
    layerID:{
      handler(newVal){
        this.popup.setPosition(undefined);
        if(this.vectorIDs.length > 0 && newVal.length > 0){
          //图层目录树的增减有待优化--zpm-2023-2-14
          for (let a = 0; a < this.vectorIDs.length; a++) {
            if(newVal.indexOf(this.vectorIDs[a]) == -1){
              this.removeLayerById(this.vectorIDs[a]);
              this.vectorIDs.splice(a,1);
            }    
          }
          for (let b = 0; b < newVal.length; b++) {
            if(this.vectorIDs.indexOf(newVal[b]) == -1){
              this.addVectorLayer(this.vectorData[newVal[b]],newVal[b])
              this.vectorIDs.push(newVal[b]);
            }          
          }
        }else if(this.vectorIDs.length > 0 && newVal.length == 0){
          let count = this.vectorIDs.length
          for (let a = 0; a < count; a++) {
              this.removeLayerById(this.vectorIDs[0]);
              this.vectorIDs.splice(0,1);
          }
        }else{
          this.vectorIDs = newVal;
          for (let i = 0; i < this.vectorIDs.length; i++) {
            let layerid = this.vectorIDs[i];
            this.addVectorLayer(this.vectorData[layerid],layerid);
          }
        }
      }
    },
    mapType:{
      handler(newVal,oldVal){
        this.removeLayerById(oldVal.split(',')[0]);
        this.removeLayerById(oldVal.split(',')[1]);
        const { tileLayer, tileMark } = this.tianditu(newVal.split(',')[0],newVal.split(',')[1]);
        this.map.addLayer(tileLayer);
        this.map.addLayer(tileMark);
        this.type = newVal;
      }
    }
 },
  computed: {
    getVideo(){
      return function(v){
        return `http://localhost/D/work/tlu/%E4%B8%AD%E6%B1%87/%E8%A7%86%E9%A2%91/${v}`;
      }
    }
    // getVideo(){
    //   return require('@/assets/video/TGZ23002.mp4');
    //   }
  }
};
</script>

