      layerName: 'tlu_crtd_2023',
      outFields: '辖区,地块编号,地块位置,供应方式,用地性质,供应时间,面积（亩）,备注,全景影像,土地推介'
    };
    getVectorLayerByID(params).then(res =>{
      console.log('测试1');
      console.log(res);
      this.crtdDataNew = 
        [
          {
            id: 2023,
            data: [
              {
              id: 20231,
              label:"第一季度",
              data: {
                        "type": "FeatureCollection",
                        "name": "2023new",
                        "crs": { "type": "name", "properties": { "name": "urn:ogc:def:crs:OGC:1.3:CRS84" } },
                        "features": res.features
                      }
              }
            ]
          },
      ];
    this.initData(this.crtdDataNew);
    });
  },
  methods:{
   initData(data){
    this.vectorData = {},
    this.vectorIDs = [],
    this.treeData = [];
    let treeDataArr = [];
    for (let i = 0; i < data.length; i++) {
      let el = data[i];
      let celObjArr = [];
      for (let j = 0; j < el.data.length; j++) {
        let cel = el.data[j];
        let celObj = {
          id: cel.id,
          label: cel.label
        };
        celObjArr.push(celObj)
        this.vectorData[cel.id] = cel.data;
        this.vectorIDs.push(cel.id);
      }
      let elObj = {
        id: el.id,
        label: `${el.id}年`,
        children: celObjArr
      }
      treeDataArr.push(elObj);
    }
    this.treeData = treeDataArr;
   },
   getTreeData(data){
    let treeDataArr = [];
    for (let i = 0; i < data.length; i++) {
      let el = data[i];
      let celObjArr = [];
      for (let j = 0; j < el.data.length; j++) {
        let cel = el.data[j];
        let celObj = {
          id: cel.id,
          label: cel.label
        };
        celObjArr.push(celObj)
      }
      let elObj = {
        id: el.id,
        label: `${el.id}年`,
        children: celObjArr
      }
      treeDataArr.push(elObj);
    }
    return treeDataArr;
   },
   getLayerIds(ids){
    this.vectorIDs = [];
    for (let i = 0; i < ids.length; i++) {
      if(ids[i]>10000){
        this.vectorIDs.push(ids[i]) 
      }
    }
   },
   changeMaptype(a){
    this.type = a;
    console.log(a)
   }
  }
}
